<!--
 * @Description: +++ 
 * @Author: mhf
 * @Date: 2023/11/27 22:00
-->
<template>
  <div class="ytPageComp">
    <div class="search-box">
      <!--  -->
      <collapseTransition mode="out-in">
        <yt-form
          class="formComponent"
          v-show="showSearch"
          v-if="formLabel.length > 0"
          :inline="inline"
          :form="paginationConfig"
          :form-label="formLabel"
          :form-rules="formRules"
          :form-size="formSize"
          :label-width="labelWidth"
          :show-operation-btn="showOperationBtn"
          :reset-btn-name="resetBtnName"
          :search-btn-name="searchBtnName"
          @selectClick="selectClick"
          @handleSearch="handleSearch"
          @handleResetFrom="handleResetFrom"
        ></yt-form>
      </collapseTransition>
      <!--  -->

      <!--  -->
      <div class="lineH" v-show="showSearch" v-if="formLabel.length > 0" />
      <!--  -->

      <!--  -->
      <div
        class="tools-config dfr"
        v-if="btnList.length > 0 || formLabel.length > 0"
      >
        <!--  -->
        <div class="left">
          <yt-table-btn
            v-if="btnList.length > 0"
            :btn-list="btnList"
            :permiArr="permiArr"
            @sendClick="changeBtn"
          >
          </yt-table-btn>
        </div>
        <!--  -->

        <!-- / -->
        <div class="right">
          <yt-is-show-form
            v-if="formLabel.length > 0"
            :show-search.sync="showSearch"
          >
          </yt-is-show-form>
        </div>
        <!-- / -->
      </div>
      <!--  -->
    </div>

    <!--  -->
    <!--    <transition name="page-transition" mode="out-in">-->
    <yt-table
      v-if="showTable"
      class="tableComponent"
      ref="tableComponent"
      :table-data="tableData"
      :table-config="tableConfig"
      :table-data-column="tableDataColumn"
      :height="tableHeight"
      :idName="idName"
      :paginationConfig="paginationConfig"
      :rowStyle="rowStyle"
      @handleSelectionChange="handleSelectionChange"
    >
      <template
        v-for="item in tableSlotArr"
        :slot="item.value"
        slot-scope="scope"
      >
        <slot :name="item.value" :row="scope.row" />
      </template>
    </yt-table>
    <!--    </transition>-->
    <!--  -->

    <!--  -->
    <div class="paginationComponent dfr">
      <yt-pagination
        v-if="total > 0"
        :total="total"
        :page="paginationConfig.pageNum"
        :limit="paginationConfig.pageSize"
        :page-sizes="pageSizes"
        :paginationColor="paginationColor"
        @pagination="handleChangePagination"
      >
      </yt-pagination>
    </div>
    <!--  -->
  </div>
</template>
<script>
import { debounce, isEmptyArray } from "../../utils/publicFun";

export default {
  name: "ytPageComp",
  components: {},
  props: {
    inline: {
      type: Boolean,
      default: true,
    }, //  true / false
    formLabel: {
      type: Array,
      default: () => [],
    }, // 
    formRules: {
      type: Object,
      default: () => {},
    }, // 
    labelWidth: {
      type: String,
      default: "",
    }, // label
    formSize: {
      type: String,
      default: "small",
    }, //  small / mini / medium
    showOperationBtn: {
      type: Boolean,
      default: true,
    }, //  ()
    searchBtnName: {
      type: String,
      default: "",
    }, // 
    resetBtnName: {
      type: String,
      default: "",
    }, // 
    btnList: {
      type: Array,
      default: () => [],
    }, // ()
    permiArr: {
      type: Array,
      default: () => [],
    }, // 

    tableData: {
      type: Array,
      default: () => [],
    }, // 
    tableConfig: {
      type: Object,
      default: () => {
        return {
          loading: false,
          stripe: true, //  table
          border: true, // 
          resizable: true, //  el-table  border 
          isAddIndex: true, // true1011
        };
      },
    }, // 
    tableDataColumn: {
      type: Array,
      default: () => [],
    }, // 
    total: {
      type: Number,
      default: 0,
    }, // 
    paginationConfig: {
      type: Object,
      default: () => {
        return {
          pageNum: 1,
          pageSize: 10,
        };
      },
    }, // 
    pageSizes: {
      type: Array,
      default: () => [5, 10, 15, 20],
    }, // 
    idName: {
      type: String,
      default: "id",
    }, //  id
    rowStyle: {
      type: Object,
      default: () => {
        return {};
      },
    },
    paginationColor: {
      type: Object,
      default() {
        return {
          "--activeBgColor": "#409eff", // 
          "--fontColor": "#ffffff", // 
          "--hoverColor": "#409eff", // 
        };
      },
    }, // 
    noSelection: {
      type: String,
      default: "",
    }, // 
  },
  watch: {
    showSearch(newValue) {
      let ac = document.querySelector(".ytPageComp"); // 
      let sb = document.querySelector(".search-box"); //  + 
      let pc = document.querySelector(".paginationComponent"); // 
      if (!newValue) {
        /*  */
        setTimeout(() => {
          this.tableHeight = `calc(${ac.clientHeight}px - ${sb.clientHeight}px - ${pc.clientHeight}px)`;
        }, 301);
      } else {
        /*  */
        setTimeout(() => {
          this.tableHeight = `calc(${ac.clientHeight}px - ${sb.clientHeight}px - ${pc.clientHeight}px)`;
        }, 301);
      }
    },
  },
  computed: {
    tableSlotArr() {
      /* [tableDataColumnisSlot===true] */
      return this.tableDataColumn.filter((item) => item.isSlot);
    }, // 
  },
  data() {
    return {
      showSearch: true, // 
      selectionObj: {
        ids: [], //  id
        selection: [], // 
      },
      tableHeight: "0", // 
      resizeE: null, // 
      showTable: false, // 
    };
  },
  methods: {
    /**
     * @Event yt-form  
     * @description: 
     * @param: item: formLabelitems: formLabelopt
     * @author: mhf
     * @time: 2023-10-29 13:30:25
     **/
    selectClick(selected) {
      this.$emit("selectClick", selected);
    },

    /**
     * @Event yt-form 
     * @description: 
     * */
    handleSearch(searchData) {
      let updateData = {
        ...searchData,
        pageNum: 1,
        pageSize: 10,
      };
      this.$emit("update:paginationConfig", updateData);
      this.getList();
    },

    /**
     * @Event yt-form 
     * @description: 
     * @more:  .sync:update
     * */
    handleResetFrom() {
      let updateData = {
        pageNum: 1,
        pageSize: 10,
      }; // 
      this.$emit("update:paginationConfig", updateData);
      this.$refs.tableComponent.handleResetSelection();
      this.$emit("resetForm"); // 
      this.getList();
    },

    /**
     * @Event 
     * @description:   
     * @use: @getTableData="Event"
     * */
    async getList() {
      // this.showTable = false;
      await this.$emit("getTableData");
      // this.$nextTick(() => {
      //   this.showTable = true;
      // });
    },

    /**
     * @Event 
     * @description: 
     * @use:  @getBtnType="getBtnType" 
     * @warning:  
     * */
    changeBtn(type) {
      this.$emit("getBtnType", type);
      if (type === "") {
        this.$emit("showPublicDialog", null, type);
      }
      if (type === "") {
        if (!isEmptyArray(this.selectionObj.ids)) {
          this.$emit("deleteRows");
        } else {
          this.$message.error(this.noSelection);
        }
      }
    },

    /**
     * @Event 
     * @description: 
     * */
    handleChangePagination(obj) {
      let updateData = {
        ...this.paginationConfig,
        pageNum: obj.page,
        pageSize: obj.limit,
      };
      this.$emit("update:paginationConfig", updateData);
      this.getList();
    },

    /**
     * @Event 
     * @description:  tableComponent 
     * @param: selection:  idName: idid
     * */
    handleSelectionChange(selection) {
      this.selectionObj = selection;
    },

    /**
     * @Event 
     * @description: 
     * */
    initTableHeight() {
      this.$nextTick(() => {
        let ac = document.querySelector(".ytPageComp"); // 
        let sb = document.querySelector(".search-box"); //  +  + 
        let pc = document.querySelector(".paginationComponent"); // 
        this.tableHeight = `calc(${ac.clientHeight}px - ${sb.clientHeight}px - ${pc.clientHeight}px)`;
        this.showTable = true;
      });
    },
  },

  created() {
    this.getList(); // 
    this.initTableHeight(); // 
    this.resizeE = debounce(this.initTableHeight, 300); // 
    window.addEventListener("resize", this.resizeE);
  },

  mounted() {},

  beforeDestroy() {
    window.removeEventListener("resize", this.resizeE);
  },
};
</script>

<style lang="scss">
.ytPageComp {
  width: 100%;
  height: 100%;
  font-size: 14px !important;
}

::v-deep .el-table .cell .el-dropdown .el-dropdown-link {
  font-size: 14px !important;
  //color: red !important;
}

.search-box {
}

.formComponent {
}

.tableComponent {
}

.paginationComponent {
  height: 50px;
  align-items: center;
  flex-direction: row-reverse;
  padding: 10px 10px 0 0;
}

.lineH {
  height: 1px;
  background: #eeeeee;
  width: 100%;
  margin-bottom: 10px;
}

.tools-config {
  justify-content: space-between;
  align-items: baseline;
  padding: 0 10px;
  min-height: 48px;

  .left {
  }

  .right {
    min-width: 84px;
  }
}
</style>
