<!--
 * @Description: el-table 
 * @Author: mhf
 * @Date: 2023/11/10 22:44
-->
<template>
  <el-table
    ref="tableRef"
    v-loading="tableConfig.loading"
    :border="tableConfig.border"
    :stripe="tableConfig.stripe"
    :resizable="tableConfig.resizable"
    :isAddIndex="tableConfig.isAddIndex"
    :height="height"
    :data="tableData"
    :expand-row-keys="expandRowKeys"
    :row-key="handleRowKey"
    @expand-change="handleExpandChange"
    @cell-dblclick="handleCellDbClick"
    @selection-change="handleSelectionChange"
    :header-row-style="headerRowStyle"
    :row-style="rowStyle"
  >
    <template v-for="(item, index) in tableDataColumn">
      <!--    index -->
      <el-table-column
        v-if="item.type === 'index'"
        type="index"
        :key="index"
        :index="indexMethod"
        :label="item.label"
        :fixed="item.fixed"
        :width="item.width"
      />

      <!--   selection -->
      <el-table-column
        v-else-if="item.type === 'selection'"
        type="selection"
        :key="index"
        :reserve-selection="true"
        :width="item.width"
        :fixed="item.fixed"
      />

      <!--   expand -->
      <el-table-column
        v-else-if="item.type === 'expand'"
        type="expand"
        :key="index"
        :width="item.width"
        :label="item.label"
        :fixed="item.fixed"
      >
        <template slot-scope="scope">
          <!-- (slotName)item.type(operationSlot) -->
          <slot :name="item.slotName || item.type" :row="scope.row" />
        </template>
      </el-table-column>

      <!--  customSlot -->
      <el-table-column
        v-else-if="item.type === 'customSlot'"
        show-overflow-tooltip
        :key="index"
        :width="item.width"
        :fixed="item.fixed"
      >
        <template slot="header" slot-scope="scope">
          <!--  -->
          <slot :name="item.slotHeader" :row="scope" />
        </template>

        <template slot-scope="scope">
          <!--  -->
          <slot :name="item.slotContent" :row="scope.row" />
        </template>
      </el-table-column>

      <!--    operationSlot -->
      <el-table-column
        v-else-if="item.type === 'operationSlot'"
        :key="index"
        :label="item.label"
        :width="item.width"
        :fixed="item.fixed"
      >
        <template slot-scope="scope">
          <!-- (slotName)item.type(operationSlot) -->
          <slot :name="item.slotName || item.type" :row="scope.row" />
        </template>
      </el-table-column>

      <el-table-column
        v-else
        :key="index"
        show-overflow-tooltip
        :label="item.label"
        :prop="item.value"
        :sortable="item.sortable"
        :width="item.width"
        :fixed="item.fixed"
      >
        <template slot-scope="scope">
          <span v-if="item.isSlot">
            <slot :name="item.value" :row="scope.row" />
          </span>
          <span v-else>{{ scope.row[item.value] }}</span>
        </template>
      </el-table-column>
    </template>
  </el-table>
</template>

<script>
export default {
  name: "ytTable",
  props: {
    tableData: {
      type: Array,
      default: () => {
        return [];
      },
    }, // 
    tableDataColumn: {
      type: Array,
      default: () => {
        return [];
      },
    }, // 
    tableConfig: {
      type: Object,
      default: () => {
        return {
          loading: false,
          stripe: true, //  table
          border: true, // 
          resizable: true, //  el-table  border 
          isAddIndex: true, // true1011
        };
      },
    }, // 
    rowStyle: {
      type: Object,
      default: () => {
        return {
          background: "#fafafa",
          boxShadow: "inset 0px -1px 0px 0px #EEEEEE",
          fontFamily: "MicrosoftYaHei",
          color: "#606266",
          height: "24px",
        };
      },
    }, // 
    headerRowStyle: {
      type: Object,
      default: () => {
        return {
          color: "#333333",
        };
      },
    }, // 
    height: {
      type: String,
      default: "45vh",
    }, // 
    idName: {
      type: String,
      default: "id",
    }, // 
    paginationConfig: {
      type: Object,
      default: () => {
        return {
          pageNum: 1,
          pageSize: 10,
        };
      },
    }, // 
    expandOnly: {
      type: Boolean,
      default: false,
    }, // 
  },
  data() {
    return {
      ids: [], // ids
      expandRowKeys: [], //  keys
      search: "",
    };
  },
  methods: {
    /**
     * @Event 
     * @description:  expandedRows expanded
     * @author: mhf
     * @time: 2023-11-12 14:08:29
     **/
    handleExpandChange(row) {
      if (!this.expandOnly) {
        return;
      }
      const rowKey = row[this.idName];
      if (this.expandRowKeys === [] || this.expandRowKeys.length === 0) {
        //  key  expandRowKeys 
        this.expandRowKeys.push(rowKey);
      } else {
        //  :  key  expandRowKeys 
        this.expandRowKeys = this.expandRowKeys.includes(rowKey)
          ? []
          : [rowKey];
      }
    },

    /**
     * @Event 
     * @description:
     * */
    handleRowKey(row) {
      return row[this.idName];
    },

    /**
     * @Event 
     * @description: ids
     * */
    handleSelectionChange(selection) {
      let ids = selection.map((row) => row[this.idName]);
      let passData = {
        ids: ids, // ids
        selection, // 
      };
      this.$emit("handleSelectionChange", passData);
    },

    /**
     * @Event 
     * @description:
     * */
    handleCellDbClick(rowData) {
      this.$emit("handleCellDbClick", rowData);
    },

    /**
     * @Event /
     * @description: rows: 
     * */
    handleResetSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.tableRef.toggleRowSelection(row);
        });
      } else {
        this.$refs.tableRef.clearSelection();
      }
    },

    /**
     * @Event 
     * @description: indexindex
     * */
    indexMethod(index) {
      if (!this.tableConfig.isAddIndex) {
        return index + 1;
      }
      return (
        index +
        this.paginationConfig.pageSize * (this.paginationConfig.pageNum - 1) +
        1
      );
    },
  },
  created() {},
};
</script>

<style lang="scss" scoped>
::v-deep .el-table th.el-table__cell {
  $table-header-bg-color: #f8f8f9;
  background-color: $table-header-bg-color;
}
</style>
