<!--
 * @Description:  
 * @Author: mhf
 * @Date: 2023/10/27 17:49
 * @todo: 
-->
<template>
  <div class="ytFileUpload">
    <el-upload
      :disabled="utilsObj.isDisabled"
      :action="actionUrl"
      :headers="headerObj"
      :file-list="utilsObj.fileList"
      :limit="utilsObj.limitNum"
      :multiple="utilsObj.isMultiple"
      :on-preview="handlePreview"
      :on-success="handleSuccess"
      :on-remove="handleRemove"
      :before-upload="handBeforeUpload"
      :on-exceed="handleExceed"
    >
      <!--  -->
      <div v-if="utilsObj.typeStyle === 0">
        <el-button
          :disabled="utilsObj.isDisabled"
          :size="utilsObj.btnSize"
          :icon="utilsObj.btnIcon"
          :type="utilsObj.btnType"
          >{{ utilsObj.btnContent }}
        </el-button>
      </div>

      <!--    -->
      <div v-if="utilsObj.typeStyle === 1">
        <slot></slot>
      </div>
    </el-upload>
  </div>
</template>

<script>
export default {
  name: "ytFileUpload",
  components: {},
  props: {
    utilsObj: {
      type: Object,
      default: () => ({
        isDisabled: false, // 
        fileList: [], // 
        limitNum: 3, //  
        fileSize: 50, // MB
        typeStyle: 0, // 
        isMultiple: false, // 
        btnSize: "medium", //  medium / small / mini
        btnIcon: "el-icon-upload", // 
        btnType: "text", //  primary / success / warning / danger / info / text
        btnContent: "", // 
      }),
    }, // 

    actionUrl: {
      type: String,
      default: "",
    }, // : process.env.VUE_APP_BASE_API + "/file/upload",

    headerObj: {
      type: Object,
      default: function () {
        return {};
      },
    }, // requestHeaderstoken {AuthorizationSys: "tokenKey"}

    uploadType: {
      type: Object,
      default: () => ({
        fileType: [
          "application/vnd.android.package-archive",
          "application/x-zip-compressed",
          "application/pdf",
          "application/msword",
          "application/vnd.openxmlformats-officedocument.wordprocessingml.document",
          "application/vnd.ms-excel",
          "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
        ], // 
        fileName: ["apk", "zip", "pdf", "doc", "docx", "xls", "xlsx"], // 
      }),
    }, //  handBeforeUpload(file) {// file.type}
  },
  data() {
    return {
      resFileArr: [], // 
    };
  },
  methods: {
    /**
     * @Event 
     * @description:
     * @author: mhf
     * @time: 2023-10-27 23:59:11
     **/
    handlePreview(file) {
      console.log(file, "");
      if (file.response) {
        //  
        window.open(file.response.data.url);
      } else {
        //  
        window.open(file.url);
      }
    },

    /**
     * @Event 
     * @description: 
     * @author: mhf
     * @time: 2023-10-27 23:59:34
     **/
    handleSuccess(file) {
      console.log(file, "");
      if (file) {
        this.resFileArr.push(file.data);
        this.$emit("getFileUploadYt", this.resFileArr);
      } else {
        this.$message.warning(file.message + "");
      }
    },

    /**
     * @Event 
     * @description: 
     * @author: mhf
     * @time: 2023-10-28 00:00:37
     **/
    handleRemove(file) {
      console.log(file, "");
      if (file.response) {
        this.resFileArr.map((item, index) => {
          if (
            item === file.response.data ||
            item.url === file.response.data.url
          ) {
            this.resFileArr.splice(index, 1);
            this.$emit("getFileUploadYt", this.resFileArr);
          }
        });
      } else {
        this.resFileArr.map((item, index) => {
          if (item === file || item.url === file.url) {
            this.resFileArr.splice(index, 1);
            this.$emit("getFileUploadYt", this.resFileArr);
          }
        });
      }
    },

    /**
     * @Event 
     * @description: 1.apkzippdfdocdocxxlsxlsx2.
     * @author: mhf
     * @time: 2023-10-28 00:01:13
     **/
    handBeforeUpload(file) {
      console.log(file, file.type, "");
      if (this.uploadType.fileType.indexOf(file.type) === -1) {
        this.$message.error(
          ` ${this.uploadType.fileName.join("")} `
        );
        return false;
      }
      if (file.size > this.utilsObj.fileSize * 1024 * 1024) {
        this.$message.error(` ${this.utilsObj.fileSize}MB`);
        return false;
      }
    },

    /**
     * @Event 
     * @description:
     * @author: mhf
     * @time: 2023-10-28 00:03:21
     **/
    handleExceed(files, fileList) {
      console.log(files, fileList, "");
      this.$message.warning(
        ` ${this.utilsObj.limitNum}  ${
          files.length
        }  ${files.length + fileList.length} `
      );
    },

    /**
     * @Event 
     * @description:
     * @author: mhf
     * @time: 2023-10-28 00:03:42
     **/
    getFileList() {
      setTimeout(() => {
        console.log(this.utilsObj, "");
        if (this.utilsObj.fileList) {
          this.resFileArr = this.utilsObj.fileList;
        }
      }, 500);
    },
  },
  created() {},
  mounted() {
    this.getFileList();
  },
};
</script>

<style lang="scss" scoped>
.ytFileUpload {
  &-fileBox {
    height: 36px;
    background: rgba(255, 255, 255, 0);
    border-radius: 4px;
    display: flex;
    align-items: center;
    color: #1492ff;
    font-weight: bold;
    font-size: 16px;

    i {
      margin-right: 5px;
    }
  }
}
</style>
